.grid-container {
  display: grid;
  grid-template-columns: repeat(10, 1fr); /* tworzy 10 kolumn */
  grid-gap: 10px; /* odstępy między elementami */
  max-width: 500px; /* maksymalna szerokość kontenera */
  margin: 20px auto; /* wycentruj kontener na stronie */
}

.grid-item {
  background-color: #efefef; /* kolor tła dla elementów siatki */
  border: 1px solid #ddd; /* obramowanie elementów siatki */
  padding: 20px; /* padding wewnątrz elementów siatki */
  text-align: center; /* wyrównanie tekstu do środka */
  line-height: 1.5; /* wysokość linii */
  font-size: 16px; /* rozmiar czcionki */
}

/* Opcjonalnie, możesz dodać responsywność */
@media (max-width: 600px) {
  .grid-container {
    grid-gap: 5px;
  }

  .grid-item {
    padding: 10px;
    font-size: 14px;
  }
}

