.grid-item {
  /* Twoje style dla elementu siatki */
  position: relative; /* potrzebne do pozycjonowania X-a */
}

.cross {
  color: red;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 2rem;
  pointer-events: none; /* aby kliknięcie przechodziło do grid-item */
}

.crossed {
  text-decoration: line-through;
}

.day-number {
  /* Twoje style dla numeru dnia */
