body {
  font-family: "Work Sans", sans-serif;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-gap: 10px;
  padding: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.grid-item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  background-color: white;
  border-radius: 16px;
  box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.grid-item.crossed {
  color: white;
  text-decoration: line-through;
  background-color: red;
}

.cross {
  position: absolute;
  font-size: 2rem;
  color: white;
}

.day-number {
  z-index: 1;
}

/* Tu możesz dodać media queries, aby responsywnie zmieniać wielkość kart */
